import React from "react";
import "./App.scss";
import Main from "./containers/Main";
import {googleAnalytics} from "./portfolio";

const injectGA = () => {
  if (typeof window == "undefined") {
    return;
  }
  window.dataLayer = window.dataLayer || [];
  function gtag() {
    window.dataLayer.push(arguments);
  }
  gtag("js", new Date());

  gtag("config", googleAnalytics.id);
};

function App() {
  return (
    <div>
      <script
        async
        src={
          "https://www.googletagmanager.com/gtag/js?id=" + googleAnalytics.id
        }
      />
      <script>{injectGA()}</script>
      <Main />
    </div>
  );
}

export default App;
